/*-----------------*/
/*--- F O N T S ---*/
/*-----------------*/

@import url('https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400..700;1,400..700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

/*-------------------------------------------*/
/*--- S I T E W I D E   C O N S T A N T S ---*/
/*-------------------------------------------*/

:root {
    /*--- standard colours ---*/
    --black: 0, 0, 0;
    --grey-dark: 16, 16, 16;
    --grey-875: 32, 32, 32;
    --grey-750: 64, 64, 64;
    --grey-625: 96, 96, 96;
    --grey-500: 128, 128, 128;
    --grey-375: 160, 160, 160;
    --grey-250: 192, 192, 192;
    --grey-125: 224, 224, 224;
    --grey-light: 239, 239, 239;
    --white: 255, 255, 255;

    /*--- design specific ---*/
    --blue-dark: 45, 61, 112;
    --blue-light: 0, 150, 211;
    --green: 197, 192, 0;
    --red: 223, 19, 28;
    --orange: 237, 103, 45;

    /*--- font sizes ---*/
    --xl: 60rem;    /* Extra Large - H1 headings, main titles */
    --l: 24rem;     /* Large - H2 headings, subtitles */
    --m: 19rem;     /* Medium - H3 headings, accents */
    --s: 16rem;     /* Small - body text, buttons */
    --xs: 14rem;    /* Extra Small - small text, captions */
}


/*-------------------*/
/*--- G L O B A L ---*/
/*-------------------*/

* {
    margin: 0;
    padding: 0;
    line-height: 1;
    box-sizing: border-box;
}

html { scroll-behavior: smooth; }

html, body {
    width: 100%;
    height: 100%;
}

body {
    display: flex;
    flex-direction: column;
    font-family: 'Poppins', sans-serif;
    font-size: var(--s);
    font-weight: 400;
    color: rgb(var(--grey-875));
}

header, main, footer {
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
}

main {
    flex: 1;
}


/*---------------------*/
/*--- H E L P E R S ---*/
/*---------------------*/

.sr-only {
    position: absolute;
    left: -10000px;
}

.hidden-onload {
    display: none !important;
}


/*-----------------------*/
/*--- D E F A U L T S ---*/
/*-----------------------*/

h1 {
    font-size: var(--xl);
    font-weight: 600;
}

h2, h3, h4 {
    font-size: var(--l);
    font-weight: 600;
}

h2 { font-size: var(--m); }

h3 { font-size: var(--m); }

h4 { font-size: var(--s); }

p, li, ol { font-weight: 300; }

a {
    color: rgb(var(--grey-875));
    text-decoration: none;
}

strong { font-weight: 600; }


/*---------------------------------------------*/
/*--- F O R M A T E D   T E X T   B L O C K ---*/
/*---------------------------------------------*/

.text h1, .text h2, .text h3, .text h4 {
    line-height: 1.2;
    margin-bottom: 0.5em;
}

.text p {
    line-height: 1.5;
    margin-bottom: 1em;
}

.text p:last-child {
    margin-bottom: 0;
}

.text ul {
    margin-bottom: 1em;
    list-style-position: inside;
}

.text ul li {
    line-height: 1.5;
}

.text strong {
    font-weight: 600;
}

.text a {
    font-weight: 400;
    color: rgb(var(--gold-dark));
}

.text a:hover { text-decoration: underline; }


/*-----------------------------*/
/*--- F O R M   F I E L D S ---*/
/*-----------------------------*/

fieldset {
    border: none;
}

ul.form {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    list-style: none;
    gap: 15rem;
}

ul.form > li {
    position: relative;
    display: flex;
    flex-basis: 100%;
    align-items: center;
    gap: 15rem;
    max-width: 100%;
}

ul.form > li.-block.-no-gap {
    margin-top: 0;
}

ul.form > li.pre-selected {
    padding: 5rem 0;
    color: rgb(var(--white));
    background-color: rgb(var(--gold));
}

ul.form > li p {
    margin: 0;
}

ul.form > li p.-note {
    font-size: var(--xs);
}

ul.form > li.gap {
    margin-top: 15rem;
}

form label { font-weight: 600;}

input + label,
textarea + label {
    position: absolute;
    top: 0.75em;
    left: 0.5em;
    padding: 0.35em 0.5em;
    font-size: var(--s);
    font-weight: 300;
    color: rgba(var(--grey-875), 0.75);
    line-height: 1;
    pointer-events: none;
    transition: 200ms ease-in-out;
    transform-origin: center left;
}

input,
select,
textarea {
    flex: 1;
    min-height: 3em;
    min-width: 3em;
    max-width: 500rem;
    padding: 0.25em 1em 0;
    font-family: 'Poppins', sans-serif;
    font-size: var(--s);
    line-height: 1.5em;
    color: rgb(var(--grey-875));
    border: none;
    border-bottom: 2px solid transparent;
    background-color: rgba(var(--grey-light));
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
}

input[type=text],
input[type=number],
input[type=email],
input[type=date],
textarea {
    width: 100%;
}

select {
    padding-right: 3em;
    background-image: url(/images/icons/dropdown-arrow.svg);
    background-position: center right;
    background-size: 3em auto;
    background-repeat: no-repeat;
}

textarea {
    padding: 20rem;
    resize: vertical;
}

input:focus,
textarea:focus {
    border-bottom: 2px solid rgb(var(--gold-light));
    outline: none;
}

input.error, textarea.error,
input.touched:invalid,
textarea.touched:invalid {
    background-color: rgba(var(--red), 0.2);
}

input.touched:invalid:focus,
textarea.touched:invalid:focus {
    border-bottom: 2px solid rgb(var(--red));
}

label.checkbox {
    display: flex;
    align-items: center;
    gap: 10rem;
    line-height: 1.3;
}

label.checkbox input { flex: 0; }

label.checkbox input:checked {
    background: url('/images/icons/check.svg') center / 2em no-repeat rgba(var(--grey-light));
}

li.pre-selected label.checkbox input:checked {
    background-color: rgb(var(--gold));
}

label.checkbox.-bold {
    font-weight: 600;
}

label.checkbox.-regular {
    font-weight: 300;
}

input[type=file] { font-size: var(--xs); }

/* auto label/title input fields */
input:focus + label,
textarea:focus + label,
input:not(:placeholder-shown) + label,
textarea:not(:placeholder-shown) + label {
    left: 1em;
    color: rgb(var(--white));
    background-color: rgb(var(--grey-500));
    transform: translateY(calc(-1.05em - 0.5em)) scale(0.6);
}

input.touched:invalid:focus + label,
textarea.touched:invalid:focus + label,
input.touched:invalid:not(:placeholder-shown) + label,
textarea.touched:invalid:not(:placeholder-shown) + label {
    background-color: rgb(var(--red));
}

input:focus + label,
textarea:focus + label {
    background-color: rgb(var(--gold));
}

/* The switch - the box around the slider */
label.switch div {
    position: relative;
    flex-shrink: 0;
    width: 70rem;
    height: 35rem;
}

/* Hide default HTML checkbox */
label.switch {
    position: relative;
    display: inline-flex;
    align-items: center;
    gap: 15rem;
    white-space: nowrap;
    cursor: pointer;
}

label.switch input {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;
}

/* The slider */
label.switch div span {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(var(--grey-light));
    border-radius: 17.5rem;
    transition: 0.4s;
}

label.switch div span:before {
    position: absolute;
    content: "";
    height: 27rem;
    width: 27rem;
    left: 4rem;
    bottom: 4rem;
    border-radius: 100%;
    background-color: white;
    transition: 0.4s;
}

label.switch input:checked + span {
    background-color: rgb(var(--gold-light));
}


label.switch input:checked + span:before {
    transform: translateX(34rem);
}


/*---------------------*/
/*--- B U T T O N S ---*/
/*---------------------*/

button {
    appearance: none;
    border: none
}

.button {
    position: relative;
    display: inline-flex;
    min-height: 45rem;
    min-width: 180rem;
    padding: 5rem 15rem;
    align-items: center;
    justify-content: center;
    font-size: var(--s);
    font-weight: 500;
    line-height: 1;
    text-decoration: none !important;
    border-radius: 4rem;
    cursor: pointer;
    transition: all ease-in-out .25s;
}

.button:hover { text-decoration: none; }

.button.-v1 {
    color: rgb(var(--blue-dark));
    border: 1px solid rgb(var(--blue-dark));
}

.button.-v2 {
    color: rgb(var(--white));
    background-color: rgb(var(--orange));
}

.button.-v1:hover, .button.-v2:hover {
    color: rgb(var(--white));
    background-color: rgb(var(--blue-dark));
}

.button.-v3 {
    color: rgb(var(--white));
    background-color: rgb(var(--blue-dark));
}

.button.-v3:hover {
    color: rgb(var(--blue-dark));
    background-color: rgb(var(--white));
    border: 1px solid rgb(var(--blue-dark));
}


/*-------------*/
/*--- N A V ---*/
/*-------------*/

#nav {
    width: 100%;
    max-width: 1280rem;
    margin: 0 auto;
    padding: 10rem;
}

#nav ul {
    display: flex;
    align-items: center;
    gap: 15rem;
    list-style: none;
}

#nav ul li.logo {
    display: flex;
    flex: 1;
    justify-content: center;
    align-items: center;
}

#nav img {
    max-width: 195rem;
}


/*-------------------*/
/*--- H E A D E R ---*/
/*-------------------*/

#header{
    position: relative;
}

#header .carousel-slide {
    width: 100%;
    aspect-ratio: 2.4;
    display: flex;
}

#header .carousel-slide img {
    width: 100%;
    object-fit: cover;
}

#header nav{
    position: absolute;
    bottom:0;
    justify-self: center;
}

#header nav ul{
    display: flex;
    justify-content: center;
    border-top-left-radius: 4rem;
    border-top-right-radius: 4rem;
    list-style: none;
    overflow: hidden;
}

#header nav ul li a{
    display: flex;
    width: 295rem;
    height: 75rem;
    align-items: center;
    justify-content: center;
    font-family: 'Lora', sans-serif;
    font-size: var(--l);
    color: rgb(var(--blue-dark));
    line-height: 1.1;
    text-align: center;
    background-color:rgb(var(--white));
    border-bottom: 5px solid;
    transition: 0.25s ease-in-out;
}

#header nav ul li:first-child a{ border-color: rgb(var(--red)); }
#header nav ul li:nth-child(2) a{ border-color: rgb(var(--orange)); }
#header nav ul li:nth-child(3) a{ border-color: rgb(var(--blue-light)); }
#header nav ul li:last-child a{ border-color: rgb(var(--green)); }

#header nav ul li a:hover,
#header nav ul li a.selected{
    color: rgb(var(--white));
    background-color: rgb(var(--blue-dark));
}

#property-search{
    display: flex;
    width: 100%;
    min-height: 75rem;
    justify-content: center;
    align-items: center;
    color: rgb(var(--white));
    background-color: rgb(var(--blue-dark));
}

#property-search ul{
    display: flex;
    gap: 15rem;
    list-style: none;
}